<template>
  <div rel="website-mainpage">
    <div class="content">
      <div>
        <!-- <picture>
          <img src="../assets/img/test.png" alt="logo"/>
        </picture> -->
        <el-carousel indicator-position="outside">
          <el-carousel-item v-for="(item, index) in carouselLists" :key="index">
            <picture>
              <img class="carouselitem" :class="[item.circle ? 'circle': '', item.ellipse ? 'ellipse': '']" :src="item.src"/>
            </picture>
          </el-carousel-item>
        </el-carousel>
      </div>
      <div class="card-wrapper">
        <el-row :gutter="60">
          <el-col :span="8" v-for="(item, index) in cardList" :key="index">
            <el-card :body-style="{ padding: '0px' }">
              <div class="container-3d">
                <div :class="['wrapper-3d', item.fliped ? 'fliped' : '']">
                  <div class="up">
                    <div class="card-content" @click="toggleFlipCard(index)" >
                      <h3>{{ item.title }}</h3>
                      <div class="description">
                        {{ item.description }}
                      </div>
                      <div class='box'>
                      </div>
                    </div>
                    <div class="card-bottom">
                      <div class="bottom clearfix">
                        <span><a :href="item.href">了解详情</a></span>
                      </div>
                    </div>
                  </div>
                  <div class="down">
                    <div class="card-content" @click="toggleFlipCard(index)" >
                      <h3>{{ item.title }}</h3>
                      <div class="major">
                        负责人：{{ item.major }}
                      </div>
                      <div class='member'>
                        <div class="member-label">
                          成&nbsp;&nbsp;&nbsp;员：
                        </div>
                        <ul>
                          <li v-for="(member, index) in item.members" :key="index">{{member}}</li>
                        </ul>
                      </div>
                      <!-- <div class='box'>
                      </div> -->
                      <img :src="item.icon"/>
                    </div>
                    <div class="card-bottom">
                      <div class="bottom clearfix">
                        <span><a :href="item.href">了解详情</a></span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div>
      <!-- <iframe width="100%" height="300" src="//jsfiddle.net/a22a6jrq/embedded/js,html,css,result/dark/" allowfullscreen="allowfullscreen" frameborder="0"></iframe> -->
    </div>
  </div>
</template>

<script>
  import pZhouyu from '../assets/img/zhouyu.png'
  import pZhouqian from '../assets/img/zhouqian.png'
  import pSundong from '../assets/img/sundong.png'
  import pLiyena from '../assets/img/liyena.png'
  import pZhanmei from '../assets/img/zhanmei.png'
  import pDenghongren from '../assets/img/denghongren.png'
  import pTest from '../assets/img/test.png'
  import pWmda from '../assets/img/wmda.png'
  export default {
    name: 'website-mainpage',
    created () {
      this.cardList.map((item) => {
        switch (item.major) {
          case '周瑜':
            item.icon = pZhouyu
            break
          case '李野娜':
            item.icon = pLiyena
            break
          case '詹梅':
            item.icon = pZhanmei
            break
          case '周倩':
            item.icon = pZhouqian
            break
          case '邓虹任':
            item.icon = pDenghongren
            break
          case '孙东':
            item.icon = pSundong
            break
        }
      })
    },
    data () {
      return {
        fliped: false,
        carouselLists: [{
          src: pTest
        }, {
          src: pWmda,
          ellipse: true
        }, {
          src: pTest
        }],
        cardList: [{
          title: '公共组件',
          description: '公共组件为58各业务线提供接入服务，包含TopBar、隐私通话等',
          href: 'http://www.baidu.com',
          major: '周瑜',
          members: ['周瑜', '李兆丹'],
          icon: 'zhouyu.png'
        }, {
          title: 'PASSPORT',
          description: 'PASSPORT为58各业务线提供统一登录认证的接入服务。',
          href: 'http://www.baidu.com',
          major: '李野娜',
          members: ['李野娜', '邓虹任', '杨兰'],
          icon: 'liyena.png'
        }, {
          title: 'IM',
          description: 'IM为58各业务线商家与客户的会话提供接入服务。',
          href: 'http://www.baidu.com',
          major: '詹梅',
          members: ['詹梅', '陈虞付'],
          icon: 'zhanmei.png'
        }, {
          title: '智能客服',
          description: '智能客服是前端技术部开发的智能聊天机器人，可以回答用户的很多很多问题？？',
          href: 'http://www.baidu.com',
          major: '周倩',
          members: ['周倩', '李野娜', '李兆丹', '陈虞付'],
          icon: 'zhouqian.png'
        }, {
          title: '个人中心',
          description: '个人中心是XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX',
          href: 'http://www.baidu.com',
          major: '邓虹任',
          members: ['邓虹任', '李野娜', '杨兰'],
          icon: 'denghongren.png'
        }, {
          title: 'WMDA',
          description: 'WMDA是前端技术部参与的58用户行为分析平台联合项目,用户采集和展示用户行为。',
          href: 'http://wiki.58corp.com/index.php?title=WMDA%E6%95%B0%E6%8D%AE%E9%87%87%E9%9B%86%EF%BC%88web%E7%AB%AF%EF%BC%89',
          major: '周瑜',
          members: ['周瑜', '李怡明', '林雨'],
          icon: 'zhouyu.png'
        }, {
          title: 'Arthur上线系统',
          description: 'Arthur上线系统是前端技术部推出的基于git的上线系统，具有方便、快捷的优点。',
          href: 'http://www.baidu.com',
          major: '孙东',
          members: ['孙东', '周瑜', '殷圣奎', '李怡明', '林雨'],
          icon: 'sundong.png'
        }, {
          title: '每周例会',
          description: '前端技术部每周例会的分享内容、会议纪要等',
          href: 'http://www.baidu.com',
          major: '孙东',
          members: ['孙东'],
          icon: 'sundong.png'
        }]
      }
    },
    methods: {
      toggleFlipCard (index) {
        let _ = this
        _.cardList = _.cardList.map((item, i) => {
          if (index === i) {
            item.fliped = !item.fliped
          }
          return item
        })
        // console.log(!this.cardList[index].fliped)
        // this.cardList[index].fliped = !this.cardList[index].fliped
        // console.log(this.cardList)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
img{
  display: block;
  margin: 0 auto;
}
img.carouselitem {
  width: 930px;
  height: 491px;
  /* clip-path: ellipse(50% 50%); */
}
img.carouselitem.ellipse {
  clip-path: ellipse(50% 50%);
}
img.carouselitem.circle {
  clip-path: circle(50%);
}
.card-content {
  width: 100%;
  height: 400px;
  h3 {
    margin: 15px 0px;
  }
  /* position: relative; */
  .description {
    height: 145px;
    padding: 5px 10px;
    /* text-align: left; */
  }
  .major {
    height: 25px;
    line-height: 25px;
    padding: 0px 10px 0px 20%;
    text-align: left;
  }
  .member {
    height: 120px;
    padding: 5px 10px;
    position: relative;
    .member-label {
      position: absolute;
      top: 0;
      left: 0;
      height: 25px;
      line-height: 25px;
      padding: 0px 10px 0px 20%;
    }
    ul {
      text-align: left;
      margin-left: 30%;
    }
  }
  img {
    width: 200px;
    height: 200px;
    clip-path: circle(50%)
  }
}
.card-wrapper {
  margin: 60px 15%;
  .el-col {
    margin-bottom: 50px;
  }
}
.content {
  width: 100%;
  min-height: 800px;
  margin-top: 100px;
}
.el-card {
  transition: all .3s ease-in-out;
}
.el-card:hover {
  transform: scale(1.1, 1.1);
}
.card-bottom {
   padding: 14px;
   border-top: 1px solid #eaeefb;
   color: #409eff;
}
.card-bottom a {
  color: #409eff;
  text-decoration: none;
}
.card-bottom a:hover{
  color: white;
}
.card-bottom:hover {
  background-color: #409eff;
}
.card-bottom:hover a {
  color: white;
}
.box {
  margin: 0px auto;
  /* position: absolute; */
  bottom: 0;
  /* left: 50%; */
  width: 200px;
  height: 200px;
  border-radius: 50%;
  /* margin-left: -100px; */
  background-color: purple;
}
.container-3d {
  perspective: 800px;
}
.wrapper-3d {
  transform-style: preserve-3d;
  transition: transform 1s;
  /* transform-origin: right center; */
  height: 468px;
  position: relative;
}
.up {
  /* background-color: red; */
  width: 100%;
  height: 100%;
  position: absolute;
  backface-visibility: hidden;
  top: 0;
  left: 0;
}
.down {
  /* background-color: blue; */
  width: 100%;
  height: 100%;
  position: absolute;
  backface-visibility: hidden;
  transform: rotateY(180deg);
  top: 0;
  left: 0;
}
.fliped {
  transform: rotateY( 180deg );
}
</style>



