<template>
  <div id="app">
    <website-header/>
    <div class="container">
      <router-view/>
    </div>
    <website-footer/>
  </div>
</template>

<script>
import websiteFooter from './components/website-footer.vue'
import websiteHeader from './components/website-header.vue'
export default {
  name: 'app',
  components: {
    'website-footer': websiteFooter,
    'website-header': websiteHeader
  }
}
</script>
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
html, body {
  padding: 0;
  margin: 0;
  min-width: 1200px;
}
a {
  text-decoration: none;
  color: white;
}
.container {
  min-height: 600px;
  margin-top: 100px;
  margin-bottom: 80px;
  /* margin-bottom: 240px; */
}
::-webkit-scrollbar{
  width: 3px;
  height: 10px;
}
::-webkit-scrollbar-track-piece{
  -webkit-border-radius: 3px;
}
::-webkit-scrollbar-thumb:vertical{
  height: 5px;
  -webkit-border-radius: 3px;
  background-color: #666;
}
::-webkit-scrollbar-thumb:horizontal{
  width: 5px;
  -webkit-border-radius: 3px;
  background-color: #666;
}
.el-carousel .el-carousel__container {
  height: 600px;
}
</style>



