<template>
  <div rel='website-meeting'>
    <ul class='live'>
      <li v-for="(item, index) in meetingList" :key="index">
        <a :href="item.href">{{item.title + '(by ' + item.author + ') —— ' + item.date}}</a>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'website-meeting',
    data () {
      return {
        meetingList: [{
          title: '使用ES6进行代码优化',
          date: '2017-11-10',
          href: 'http://www.baidu.com',
          author: 'linyu04'
        }, {
          title: '智能客服总结',
          date: '2017-11-10',
          href: 'http://www.baidu.com',
          author: 'liyena'
        }, {
          title: 'webpack调试技巧',
          date: '2017-11-10',
          href: 'http://www.baidu.com',
          author: 'liyiming04'
        }, {
          title: 'xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx',
          date: '2017-11-10',
          href: 'http://www.baidu.com',
          author: 'linyu04'
        }]
      }
    }
  }
</script>

<style scoped lang='scss'>
div[rel='website-meeting'] {
  position: relative;
  margin: 0;
  min-height: 600px;
  display: block;
  /* background-color: purple; */
  ul {
    text-align: left;
    vertical-align: top;
    /* background: rgba(0, 0, 0, 0.2); */
    color: rgba(64, 158, 255, 0.8);
    border-radius: 5px;
    padding: 1.5em;
    margin: 2%;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
    li a{
      text-decoration: none;
      color: rgba(64, 158, 255, 0.8);
    }
  }
  .live {
    overflow: hidden;
  }
  .live > li {
    list-style: none;
    position: relative;
    padding: 0 0 0 2em;
    margin: 0 0 .5em 10px;
    transition: .12s;
  }
  .live > li::before {
    position: absolute;
    content: '\2022';
    font-family: Arial;
    color: #409eff;
    top: 0;
    left: 0;
    text-align: center;
    font-size: 2em;
    opacity: .5;
    line-height: .75;
    transition: .5s;
  }
  .live > li:hover {
    color: #409eff;
  }
  .live > li a:hover {
    color: #409eff;
  }
  .live > li:hover::before {
    transform: scale(2);
    opacity: 1;
    text-shadow: 0 0 4px;
    transition: .1s;
  }
}
</style>



