<template>
  <div rel='website-footer'>
    <div class='footer'>
      <div class="footer-container">
        <el-row :gutter="60">
          <el-col :span="9">
            <el-card :body-style="{ padding: '0px' }">
              <div class="footer-links">
                <h3>友情链接</h3>
                <ul class='live'>
                  <li v-for="(item, index) in linkLists" :key="index"><a :href="item.href">{{item.name}}</a></li>
                </ul>
              </div>
            </el-card>
          </el-col>
          <el-col :span="9">
            <el-card :body-style="{ padding: '0px' }">
              <div>
                <h3>服务与支持</h3>
              </div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card :body-style="{ padding: '0px' }">
              <div>
                <h3>联系我们</h3>
                <div class="arrow"></div>
                <img src='../assets/img/erweima.png'/>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'website-footer',
    data () {
      return {
        linkLists: [{
          name: 'Vue',
          href: 'https://cn.vuejs.org/'
        }, {
          name: 'Vuex',
          href: 'https://vuex.vuejs.org/zh-cn/'
        }, {
          name: 'Vuex-router',
          href: 'https://router.vuejs.org/zh-cn/'
        }, {
          name: 'WMDA-58用户行为分析平台',
          href: 'http://wmda.58corp.com'
        }, {
          name: 'Arthur-前端上线系统',
          href: 'http://fe.58corp.com'
        }]
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
.footer {
    width: 100%;
    background-color: rgb(84, 92, 100);
    height: 240px;
    .footer-container {
      margin: 20px 200px;
      height: 200px;
    }
    .el-card {
      background-color: #545c64;
      border: none;
      box-shadow: none;
      color: white;
    }
    .arrow {
      width: 0px;
      height: 0px;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-bottom: 8px solid white;
      margin: 0 auto;
    }
    img {
      width: 150px;
      height: 150px;
    }
    .footer-links {
      h3 {
        text-align: left;
        padding-left: 20px;
      }
      ul.live {
        text-align: left;
        vertical-align: top;
        overflow: hidden;
        padding-left: 0px;
      }
      .live > li {
        list-style: none;
        position: relative;
        padding: 0 0 0 2em;
        margin: 0 0 .5em 10px;
        transition: .12s;
      }
      .live > li::before {
        position: absolute;
        content: '\2022';
        font-family: Arial;
        color: #FFF;
        top: -2px;
        left: 0;
        text-align: center;
        font-size: 2em;
        opacity: .5;
        line-height: .75;
        transition: .5s;
      }
      .live > li:hover {
        color: #FFF;
      }
      .live > li a:hover {
        color: #FFF;
        transform: scale(2);
      }
      .live > li:hover::before {
        transform: scale(2);
        opacity: 1;
        text-shadow: 0 0 4px;
        transition: .1s;
      }
    }
}
</style>



