<template>
  <div rel='website-header'>
    <el-row>
      <el-col :span="6">
          <!-- <img src='../assets/img/logo.png' height="60px"> -->
          <div class="test"><router-link to='/'>前端技术部</router-link></div>
      </el-col>
      <el-col :span="18">
          <el-menu
              class="el-menu-demo"
              mode="horizontal"
              :default-active = 'activeIndex'
              @select="handleSelect"
              background-color="#545c64"
              text-color="#fff"
              active-text-color="#ffd04b">
              <!-- <el-menu-item index="1">公共组件</el-menu-item> -->
              <el-submenu index="1">
                <template slot="title">公共组件</template>
                <el-menu-item index="1-1">TOOPBAR</el-menu-item>
                <el-menu-item index="1-2">隐私通话</el-menu-item>
              </el-submenu>
              <el-submenu index="2">
                <template slot="title">智能客服</template>
                <el-menu-item index="2-1">帮帮</el-menu-item>
                <el-menu-item index="2-2">管理后台</el-menu-item>
              </el-submenu>
              <el-submenu index="3">
                <template slot="title">PASSPORT</template>
                <el-menu-item index="3-1">PC端</el-menu-item>
                <el-menu-item index="3-2">M端</el-menu-item>
              </el-submenu>
              <el-submenu index="4">
                <template slot="title">IM</template>
                <el-menu-item index="4-1">PC端</el-menu-item>
                <el-menu-item index="4-2">M端</el-menu-item>
              </el-submenu>
              <el-menu-item index="5">个人中心</el-menu-item>
              <el-menu-item index="website-wmda">WMDA</el-menu-item>
              <el-menu-item index="5">Arthur上线系统</el-menu-item>
              <el-menu-item index="website-meeting">每周例会</el-menu-item>
          </el-menu>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: 'website-header',
    data () {
      return {
        activeIndex: 'none',
        currentDate: new Date()
      }
    },
    methods: {
      handleSelect (key, keyPath) {
        // console.log(key, keyPath)
        if (key === this.$route.name[0]) {
          return
        }
        this.$router.push({name: keyPath.slice(-1)})
        console.log(this.$route)
        this.activeIndex = this.$route.name[0]
      }
    },
    watch: {
      '$route.path': function () {
        console.log(this.activeIndex)
        this.activeIndex = this.$route.name[0]
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
div[rel='website-header'] {
  position: fixed;
  width: 100%;
  min-width: 1200px;
  left: 0;
  top: 0;
  z-index: 1500;
  background-color: rgb(84, 92, 100);
  .el-menu-demo {
    float: right;
  }
  .test {
      height: 60px;
      line-height: 60px;
      font-size: 24px;
      color: white;
      background: url('../assets/img/logo.png') fixed no-repeat;
      background-size:auto 60px;
      background-position: 20px 0px;
      text-align: left;
      padding-left: 160px;
  }
  .el-menu-item {
      text-align: left;
      min-width: auto;
  }
}
</style>



