import Vue from 'vue'
import Router from 'vue-router'
import websiteMainpage from '@/views/website-mainpage'
import websiteMeeting from '@/views/website-meeting'
import websiteWmda from '@/views/website-wmda'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'website-mainpage',
      component: websiteMainpage
    }, {
      path: '/meeting',
      name: 'website-meeting',
      component: websiteMeeting
    }, {
      path: '/wmda',
      name: 'website-wmda',
      component: websiteWmda
    }
  ]
})



